.work{
  width: 90%;
  margin: auto;
  display: flex;
  flex-direction: column;
  .blue-square {
    padding: 8rem 1rem 2.5rem 1rem;
  }
  .bs-right {
    top: -7.5rem;
    left: 0.5rem;
    z-index: 0;
  }
  .bs-left {
    top: -7.5rem;
    left: -0.5rem;
    z-index: 0;
  }
  img {
    z-index: 2;
    width: 90%;
  }
  .btSpot{
    position: relative;
    top:-2.5rem;
    display: flex;
    justify-content: space-around;
    Z-index:1;
    button{
        background-color: $darkgray;
    }
}
}
.W-left{
    img{
        align-self: flex-end;
    }
}


@media only screen and (min-width: 992px) {
    .work{
        margin-bottom: 1rem;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        .blue-square {
        padding: 2rem 3rem;
        }
        .bs-right {
        top: 0rem;
        left: -2rem;
        }
        .bs-left{
            top: 0rem;
        }
        img {
        width: 40%;
        }
        .btSpot{
            position: relative;
            top:-2.6rem;
            display: flex;
            justify-content: space-around;
        }
    }
    .W-left{
        img{
            position: relative;
            left:-2rem;
        }
    }
}